<template>
   <div class='root'>
      <action-bar title='搜索'></action-bar>
      <div class='container'>
         <scroll-view class='content'>
            <div class="title-margin">
               <div class="title">订单编号</div>
               <input type="text" ref="orderAlias" class="common-input" placeholder="请输入订单编号" />
            </div>
            <div class="title-margin">
               <div class="title">货位号</div>
               <input type="text" ref="goodsStockNo" class="common-input" placeholder="请输入货位号" />
            </div>
            <div class="title-margin">
               <div class="title">配送方式</div>
               <div class="btn-group">
                  <div class="btn-box" v-for="(item, index) in trafficList" :key="item.id">
                     <div :class="trafficIndex == index ? 'btn-checked' : 'btn-uncheck'"
                        @click="bindTraffic(item.name, index)">{{ item.name }}</div>
                  </div>
               </div>
            </div>

            <div class="title-margin">
               <div class="title">路线</div>
               <div class="btn-group">
                     <div class="btn-box" v-for="(item,index) in routeList" :key="index">
                        <div :class="routeIndex == index ? 'btn-checked':'btn-uncheck'" @click="bindRoute(item.id,index)">{{ item.name }}</div>
                     </div>
               </div>
            </div>
         </scroll-view>
      </div>
      <div class="bottom">
         <div class="search-btn" @click="bindSearch">搜索</div>
      </div>

   </div>
</template>
<script>
import { findLogisticsList, findRouteList } from '@/http/api'
export default {
  name: 'orderScreen',
  components: {

  },
  data () {
    return {
      nowDate: '',
      allTime: true, // 是否查询全部时间
      type: '', // 单据类型
      trafficList: [],
      trafficIndex: 0,
      trafficId: '',
      routeList: [],
      routeIndex: 0,
      routeId: '',
      goodsStockNo: '',
      orderAlias: ''
    }
  },
  methods: {
    // 获取配送方式
    queryTraffic () {
      findLogisticsList().then(res => {
        const trafficList = this.trafficList
        trafficList.push({
          name: '全部',
          id: ''
        })
        trafficList.push(...res.data)
        let trafficIndex = 0
        for (let i = 0; i < trafficList.length; i++) {
          const item = trafficList[i]
          if (item.name === this.trafficId) {
            trafficIndex = i
            break
          }
        }
        this.trafficList = trafficList
        this.trafficIndex = trafficIndex
      })
    },
    // 获取路线
    queryRoute () {
      findRouteList().then(res => {
        const routeList = this.routeList
        routeList.push({
          name: '全部',
          id: ''
        })
        routeList.push(...res.data)
        let routeIndex = 0
        for (let i = 0; i < routeList.length; i++) {
          const item = routeList[i]
          if (item.id === this.routeId) {
            routeIndex = i
            break
          }
        }
        this.routeList = routeList
        this.routeIndex = routeIndex
      })
    },
    bindTraffic (name, index) {
      this.trafficIndex = index
      this.trafficId = (index === 0) ? '' : name
    },
    bindRoute (id, index) {
      this.routeIndex = index
      this.routeId = id
    },
    bindSearch () {
      const orderScreen = {
      //   date: (this.data.allTime) ? '' : this.nowDate,
        goodsStockNo: this.goodsStockNo,
        orderAlias: this.orderAlias,
        typeOfTraffic: this.trafficId,
        rId: this.routeId
      }
      this.$route.params.orderScreen = orderScreen
      this.$router.back()
    }
  },
  mounted () {
    this.queryTraffic()
    this.queryRoute()
  }
}
</script>
<style lang="scss" scoped>
.root {
   width: 100vw;
   height: 100vh;
   display: flex;
   flex-direction: column;
}

.container {
   flex-grow: 1;
   position: relative;
}

.content {
   position: absolute;
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
   padding: .5rem;
   box-sizing: border-box;
}

.bottom {
   width: 100%;
   height: 3.125rem;
   box-sizing: border-box;
   padding: .625rem;
}

.search-btn {
   background-color: #42D18A;
   border-radius: 1.125rem;
   box-sizing: border-box;
   height: 2.375rem;
   line-height: 2.375rem;
   color: white;
   font-size: .9375rem;
   text-align: center;
}

.title-margin {
   margin-bottom: 2.0625rem;
}

.title {
   font-weight: bolder;
   margin: .1875rem 0;
   font-size: .9375rem;
}

.common-input {
   width: 100%;
   padding: .5rem 0;
   font-size: .875rem;
   box-sizing: border-box;
   border: none;
   border-bottom: .0625rem solid #E5E5E5;
}

.common-input::placeholder {
   color: #D3D3D3;
   font-size: .875rem;
}

.btn-group {
   width: 100%;
   margin-top: .5rem;
}

.btn-box {
   width: 25%;
   display: inline-block;
   padding: .1875rem;
   box-sizing: border-box;
   font-size: .75rem;
}

.btn-checked {
   height: 2.0625rem;
   line-height: 2.0625rem;
   text-align: center;
   border-radius: .375rem;
   background-color: #dafff1;
   border: solid .0625rem #42D18A;
   color: #42D18A;
   box-sizing: border-box;
   white-space: nowrap;
}

.btn-uncheck {
   white-space: nowrap;
   height: 2.1875rem;
   line-height: 2.1875rem;
   text-align: center;
   border-radius: .375rem;
   background-color: #F5F5F5;
   color: #333;
}
</style>
